* {
	box-sizing: border-box;
	color: #333;
}

.App {
	padding: 1rem;
	text-align: center;
}

.Contract {
	margin-top: 1rem;
	padding: 0.5rem;
	background: #ede7f6;
	border-radius: 1rem;
}

.Contract > h2 {
	width: fit-content;
	margin: auto;
	background-color: ivory;
	padding: 1rem;
	border-radius: 1rem;
}

.Contract-details {
	width: fit-content;
	padding: 1rem;
	margin: 1rem;
	background-color: ivory;
	text-align: left;
	border-radius: 1rem;
}

.input {
	float: left;
	margin-right: 0.5rem;
	margin-bottom: 0.4rem;
}

.btn {
	margin-bottom: 0.5rem;
	float: left;
}

button {
	padding: 0.2rem 0.5rem 0.3rem 0.5rem;
	background-color: #ede7f6;
	border-radius: 5px;
	border-style: none;
}

button:hover {
	cursor: pointer;
	background-color: #ede0d6;
}

form {
	padding-bottom: 1.5rem;
	margin-bottom: 1.5rem;
}

hr {
	clear: both;
}

#Account-info-item1 {
	margin: 0;
	padding: 0;
}

#Account-info-item2 {
	margin: 0;
	padding: 0;
}

@media (max-width: 599px) {
	.Account-info {
		display: grid;
		width: 100%;
		padding: 1% 0 1% 0;
		grid-template-columns: 100%;
		row-gap: 1rem;
		align-items: center;
		justify-content: safe;
	}

	.Account-info :nth-child(1) {
		width: inherit;
		margin: auto;
		height: 100%;
		overflow-wrap: break-word;
		background: #ede7f6;
		padding: 1.5rem;
		border-radius: 1.5rem;
	}

	.Account-info :nth-child(2) {
		width: inherit;
		margin: auto;
		height: 100%;
		overflow-wrap: break-word;
		background: #ede7f6;
		padding: 1.5rem;
		height: 100%;
		border-radius: 1.5rem;
	}

	.Contract {
		border-radius: 1.5rem;
	}

	.Contract-details {
		border-radius: 1.5rem;
		overflow-wrap: break-word;
		width: inherit;
		margin: 1rem auto 1rem auto;
	}

	.Contract > h2 {
		border-radius: 1.5rem;
	}
}

@media (min-width: 600px) {
	.Account-info {
		display: grid;
		padding: 0 0 1% 0;
		grid-template-columns: 1fr 1fr;
		grid-gap: 1rem;
		align-items: center;
	}

	.Account-info :nth-child(1) {
		background: #ede7f6;
		text-align: left;
		height: 100%;
		padding: 1.5rem;
		border-radius: 1rem;
		width: fit-content;
	}
	.Account-info :nth-child(2) {
		place-self: end;
		justify-content: center;
		align-self: start;

		background: #ede7f6;
		height: fit-content;
		padding: 1.5rem;
		width: fit-content;

		border-radius: 1rem;
	}
}
